.header {
  text-align: center;
  position: fixed;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  width: 100%;
  color: #228B22;
}

.item {
  text-decoration: none;
  color: #228B22;
  list-style: none;
  font-weight: bold;
}

.item:hover {
  color: white;
  cursor: pointer;
}